const initialState = {
  yangilik: {
    title: '',
    main: '',
    img: null,
  },
  tadbir:{
    title: '',
    main: '',
    img: null,
  }
};

const Reducer = (state = initialState, action) => {
  switch (action.type) {
    case 'ENTER_NEWS_PAGE': {
      return {
        yangilik: {
          title: action.title,
          main: action.main,
          img: action.img,
        },
      };
    }
    case 'ENTER_TADBIRLAR_PAGE': {
      return {
        tadbir: {
          title: action.title,
          main: action.main,
          img: action.img,
        },
      };
    }
    default:
      return state;
  }
};

export default Reducer;
